<template>
    <div class="info">
        <p class="title">修改基本資料</p>
        <p class="sub-title">MEMBER PORFOLE</p>
        <div class="info-input">
            <ul>
                <li>
                    <p class="input-title">會員姓名</p>
                    <el-input v-model="username" placeholder="請輸入">
                        <el-switch
                            v-model="value"
                            slot="suffix"
                            active-color="#ef6d38"
                            inactive-color="#333333">
                        </el-switch>
                    </el-input>
                </li>
                <li>
                    <p class="input-title">會員暱稱</p>
                    <el-input v-model="nickname" placeholder="請輸入">
                        <el-switch
                            v-model="value"
                            slot="suffix"
                            active-color="#ef6d38"
                            inactive-color="#333333">
                        </el-switch>
                    </el-input>
                        
                </li>
                <li>
                    <p class="input-title">公司名稱</p>
                    <el-input v-model="company" placeholder="請輸入">
                        <el-switch
                            v-model="value"
                            slot="suffix"
                            active-color="#ef6d38"
                            inactive-color="#333333">
                        </el-switch>
                    </el-input>
                </li>
                <li>
                    <p class="input-title">聯絡地址</p>
                    <el-input v-model="address" placeholder="請輸入">
                        <el-switch
                            v-model="value"
                            slot="suffix"
                            active-color="#ef6d38"
                            inactive-color="#333333">
                        </el-switch>
                    </el-input>
                </li>
                <li>
                    <p class="input-title">會員密碼</p>
                    <el-input v-model="password" placeholder="請輸入" show-password></el-input>
                </li>
                <li>
                    <p class="input-title">確認密碼</p>
                    <el-input v-model="confirmpassword" placeholder="請輸入" show-password></el-input>
                </li>
                <li>
                    <p class="input-title">統一編號</p>
                    <el-input v-model="numbers" placeholder="請輸入">
                        <el-switch
                            v-model="value"
                            slot="suffix"
                            active-color="#ef6d38"
                            inactive-color="#333333">
                        </el-switch>
                    </el-input>
                </li>
                <li>
                    <p class="input-title">手機號碼</p>
                    <el-input v-model="phone" placeholder="請輸入">
                        <el-switch
                            v-model="value"
                            slot="suffix"
                            active-color="#ef6d38"
                            inactive-color="#333333">
                        </el-switch>
                    </el-input>
                </li>
                <li style="width: 100%">
                    <p class="input-title">公開說明</p>
                    <el-input
                        type="textarea"
                        :autosize="{ minRows: 4, maxRows: 10}"
                        placeholder="请输入内容"
                        v-model="remark">
                    </el-input>
                </li>
            </ul>
                    
        </div>
        <div class="car-btn">修改會員資料</div>
    </div>
</template>

<script>
export default {
    name:'info',
    components: {
    },
    data() {
        return {
            username:'王曉明',
            nickname:'小明',
            company:'一二三股份有限公司',
            address:'台中市西屯區朝富路213號3樓之1',
            password:'123456',
            confirmpassword:'123456',
            numbers:'654321',
            phone:'0912345678',
            value:true,
        }
    }


}
</script>

<style lang='scss' scoped>
.info {
    padding: 100px 0;
    text-align: center;
    max-width: 850px;
    width: 80%;
    margin: 0 auto;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
    }
    .info-input {
        margin-top: 50px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .input-title {
            color: #ef6d38;
            padding-left: 25px;
            margin-bottom: 10px;
            text-align: left;
        }
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            li {
                margin-top: 30px;
                padding:0 10px;
                min-width: 400px;
            }
            @media screen and (max-width: 1330px) {
                max-width: 400px;
                li {
                    padding: 0;
                    width: 100%;
                }
            }
        }
        
    }  
}
.el-switch {
    margin: 20px 15px 0 0;
}

</style>


